
.NavigatorComponent{
    background-color:#F2F2F2;
    /*height:48%;*/
    margin-top:10%;
}
.navigator-tabs .tab{
    cursor:pointer;
    display:block;
    background-color: #7F7F7F;
    padding:1%;
    font-weight:bold;
    text-align:center;
}
.navigator-options-wrapper{
    margin:2%;
    border:1px solid #d2d2d2;
    /*height:78%;*/
}
.dataNavView, .TINavView{
    padding:6% 0 0 ;
    display:none;
}
.dataNavView.active, .TINavView.active{  display:block; background-color:#F2F2F2; }
.tab.dataNavView.active, .tab.TINavView.active{ background-color:#F2F2F2; }
.ontology{  cursor:pointer;  }
.ontologyList{list-style: none; margin:0; padding:0;}
.ontologyList li{
    background:url('indice-icon.png')no-repeat left top;
    height:1.5em;
    padding-left:2.2em;
    cursor:pointer;
    font-size:1.2em;
    margin-bottom:1%;
}
li.Stories.ontology{
    background:url('data:image/png;base64,')no-repeat left top;
}
li.People.ontology{
    background:url('data:image/png;base64,')no-repeat left top;
}
li.Places.ontology{
    background:url('data:image/png;base64,') no-repeat left top;
}
li.Fieldtrips.ontology{
    background:url('data:image/png;base64,')no-repeat left top;
}
li:hover.ontology{
    background-color:lightgray;
}

li.ontology.active{
    background-color: #909090;
}

.NavigationDropdownMenu{
    margin:2%;
}


