
@media screen and (min-height: 400px) {
    .PeopleView{
        height:85vh;
    }
    .info-wrap{
        height:77vh;
    }
}
@media screen and (min-height: 770px) {
    .PeopleView{
        height:87vh;
    }
    .info-wrap{
        height:80vh;
    }
}
.associatedItems .item{
    /*height:40vh;*/
    overflow-y:scroll;
}
.detail-item{
    margin:5px;
}
.person-bio{
    color:black;
    border:1px solid #d2d2d2;
    border-top:none;
    padding:3%;
    font-size:1.5em;
}
.wrapper.person{
    overflow-y: scroll;
    /*height:40%;*/
}


