.SearchComponent{
    /*padding-top:5vh;*/
    padding:5% 0 3% 2%;
    /*height:30vh;*/
    background:#ED4C67;
    margin-bottom:20%;
    margin-top:10%;
    
}

.suggestion-wrapper{
    display:none;
}
.suggestion-wrapper.active{
    display:block;
}
.SearchComponent input, .SearchComponent label{
    color:white;
    display:inline-block;
}
.SearchComponent label{
    margin-right:10px;
    margin-left:10%;
}
.SearchComponent input[type=text]{
    border: 0;
    outline: none;
    background: transparent;
    border-bottom: 2px solid white;
    -webkit-box-shadow:none;
            box-shadow:none;
    color:white;
    font-size:1.7em;
    padding:3px;
    height:1.6em;
    width:80%;
    margin-left:10%;
    margin-top:8%;
}
.SearchComponent input[type=text]:focus {
    outline: none;
}
@-webkit-keyframes suggestionsDrop {
    from {height:0}
    to {height:20vh;}
}
@keyframes suggestionsDrop {
    from {height:0}
    to {height:20vh;}
}
.SearchComponent input[type=text]:focus+.suggestions{
    -webkit-animation-name:suggestionsDrop;
            animation-name:suggestionsDrop;
    -webkit-animation-duration:1s;
            animation-duration:1s;
}
.suggestions{
    height:20vh;
    overflow-y:scroll;
    background-color:white;
    font-size:1.3em;
    width:21.8%;
    margin-left:0%;
    list-style: none;
    position:absolute;
    top:23%;
}
.suggestions li{
    padding:1% 0 1% 3%;
}
.suggestions li:hover{
    background-color:lightgrey;
    width:100%;
}
.suggestions.active{
    display:block;
}
.filters{
    width:100%;
    /*height:70%;*/
    margin-top:10%;
    background-color:#CC4159;
}


