.tabs{

}
.tab{
    display:none;
}
.tabs{
    list-style:none;
    background-color:white;
    padding-bottom:20px;
}
.tabs li{
    position:relative;
    cursor:pointer;
    display: inline;
    padding:.4vw 2vw .4vw 2vw;
    margin:1px;
    background-color: #dddddd;
    border-radius:0 0 5px 5px;
    border: 1px solid #909090;
    font-size:1em;
}
.tabs li.active{
    background-color: #ffffff;
}
.closeTabIcon{
    width:.75em;
    position:absolute;
    top:30%;
    right:5px;
}
.closeTabIcon.noClose{
    display:none;
}
.closeTabIcon:hover{
    border:1px solid black;
    border-radius:100%;
}
.view{
    /*height:80%;*/
}
.associated-items{
    cursor:pointer;
}
.pdfViewer .mgrpdf__wrapper canvas{
    height:87vh !important;
}


