.readerHolder,
.readerHolderFullscreen {
    font-size: 16px;
}
.readerHolder {
    position: absolute;
    top: 135px;
    left: 1rem;
    right: 1rem;
    bottom: 1rem;
    -webkit-box-shadow: 0 0 5px rgba(0,0,0,.3);
            box-shadow: 0 0 5px rgba(0,0,0,.3);
    -webkit-transition: all .6s ease;
    -o-transition: all .6s ease;
    transition: all .6s ease;
}
@media screen and (max-width: 600px) {
    .readerHolder {
        left: 10px;
        right: 10px;
        bottom: 10px;
    }
}
.readerHolderFullscreen {
    position: absolute;
    -webkit-transition: all .6s ease;
    -o-transition: all .6s ease;
    transition: all .6s ease;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-box-shadow: none;
            box-shadow: none;
}
.bar {
    position: absolute;
    top: 10px;
    left: 20px;
    right: 20px;
}
.logo {
    width: 330px;
    height: 104px;
    display: inline-block;
    margin-left: 2px;
}
@media screen and (max-width: 600px) {
    .logo {
        width: 250px;
        height: auto;
        display: block;
        margin: 0 auto 0px;
    }
}
.closeLink {
    font-size: 16px;
    color: #808080;
    border: none;
    background: none;
    float: right;
    margin-top: 75px;
    outline: none;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none
}
@media screen and (max-width: 600px) {
    .closeLink {
        margin: 0;
        font-size: 12px;
    }
}
.closeIcon {
    vertical-align: middle;
    display: inline-block;
    width: 30px;
    height: 30px;
    background: #666;
    margin-left: 5px;
    border-radius: 50%;
    position: relative;
    -webkit-transform: rotate(45deg);
        -ms-transform: rotate(45deg);
            transform: rotate(45deg)
}
.closeIcon:before,
.closeIcon:after {
    content:'';
    position: absolute;
    top: 50%;
    left: 50%;
    width: 60%;
    height: 2px;
    background: #fff;
    margin-left: -30%;
    margin-top: -1px;
    border-radius: 1px;
}
.closeIcon:after {
    -webkit-transform: rotate(-90deg);
        -ms-transform: rotate(-90deg);
            transform: rotate(-90deg)
}
.toggleFontSize {
    position: fixed;
    bottom: 1.5rem;
    right:  1.5rem;
    z-index: 10;
    background: #44739c;
    border: none;
    font-family: inherit;
    font-size:  inherit;
    border-radius: 2px;
    padding: .5rem;
    outline: none;
}


